<template lang="pug">
  .container-fluid
    .row
      div#sidebar(class="col-md-3 d-none d-lg-block p-0")
        .top(class="bg-primary position-relative")
          .title(class="pl-4 pb-3")
            h5 COLOR GUIDE<br>HTML COLOR CODES
            h1.m-0 SASS 色卡工具
        .bottom(class="d-flex flex-column bg-white")
          .div(class="m-4 mb-5 border rounded")
            ul#colorsheet(class="px-3 py-2 m-0")
              li(v-for='c in colorsheet') ${{ c.colorName }}: {{ c.colorCode }};
            .copyBtn(class="text-center py-2" @click="copyAll")
              i(class="fas fa-clipboard mr-2")
              | COPY ALL
      div(class="col offset-lg-3 offset-0 bg-light" style="min-height: 100vh;")
        .container(class="p-5")
          .row
            div(class="col-sm-6 col-md-4 col-lg-3 p-3" v-for="c in colorsheet")
              .color-card.rounded(:id="c.colorName" @click.prevent="copyColorCode(c)")
                .color-block(:style="{backgroundColor: c.colorCode}")
                .color-info(class="bg-white pl-2 py-2")
                  .color-name {{ c.colorName.toUpperCase() }}
                  .color-code {{ c.colorCode.toUpperCase() }}
    .addBtn
      i(class="fas fa-plus")
</template>

<script>
import $ from 'jquery'

export default {
  name: 'HelloWorld',
  data () {
    return {
      colorinit: `
        $color_bg: #E0CAD0;
        $color_pink: #CE7597;
        $color_gray: #AAB7C6;
        $color_top: #9C4215;
        $color_md_1: #F3D1BA;
        $color_md_2: #F7E1CD;
        $color_bg: #E0CAD0;
        $color_gray: #AAB7C6;
        `,
      colorsheet: [],
      tempColor: {}
    }
  },
  methods: {
    strCopy (v) {
      let $temp = $('<textarea>')
      $('body').append($temp)
      $temp.val(v).select()
      document.execCommand('Copy')
      $temp.remove()
    },
    copyColorCode (c) {
      // Copy Color Code
      this.tempColor = Object.assign({}, c)
      this.strCopy(this.tempColor.colorCode)

      // Text Animation
      let copyColor = $(`#${this.tempColor.colorName} .color-block`)
      copyColor.append('<div class="colorActive animated fadeOut">')
      setTimeout(() => copyColor.empty(), 1000)
    },
    copyAll () {
      this.strCopy(this.getColorsheet)
    }
  },
  computed: {
    colorInit () {
      let result = []
      this.colorinit.split('\n').forEach(
        (obj, index) => {
          if (obj.indexOf('color') !== -1) {
            result.push({
              colorName: obj.split(':')[0].split('$')[1],
              colorCode: obj.split(':')[1].trim().split(';')[0]
            })
          }
        }
      )
      return result
    },
    getColorsheet () {
      let result = []
      this.colorsheet.forEach(
        (c) => {
          result.push(`$${c.colorName}: ${c.colorCode};`)
        }
      )
      return result.join('\n')
    }
  },
  created () {
    this.colorsheet = this.colorInit
  }
}
</script>

<style scoped lang="scss">
h1, h5 {
  font-family: 'Playfair Display', 'source-han-serif-tc';
  font-weight: 600;
}

h1 {
  color: white;
}

h5 {
  opacity: 0.3;
}

.title {
  position: absolute;
  left: 0;
  bottom: 0;
}
</style>



